.popup {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background: rgba(0, 0, 0, 0.2);

  display: flex;
  justify-content: center;
  align-items: center;
}

.popup-inner {
  position: relative;
  padding: 32px;
  width: 100%;
  max-width: 640px;
  background-color: #fff;
}

.popup-inner .close-btn {
  position: absolute;
  bottom: 16px;
  right: 16px;
}

.popup-inner .complete-btn {
  position: absolute;
  bottom: 16px;
  right: 60px;
}

.title {
  margin-bottom: 20px;
}
